          animation-fill-mode: forwards;
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
  -webkit-transform: scale(0);
          transform: scale(0); }

@-webkit-keyframes newTile {
  from {
    -webkit-transform: scale(0);
